<template>
    <v-layout row wrap>
        <v-flex xs12>
            <v-alert flat type="info" :value="true"><h3>So, you like {{ $route.params.breed }} dog?</h3></v-alert>
        </v-flex>
        <v-flex xs12>
            <v-card>
                <v-card-title><h3>Here's a random pic of it</h3></v-card-title>
                <v-card-media :src="pic" :alt="$route.params.breed" height="350px" contain ></v-card-media>
            </v-card>
        </v-flex>
        <v-flex xs12 sm4 offset-sm4>
            <v-btn
                color="info"
                large
                block
                @click.native="getDog"
            >
                Show me another one !!
            </v-btn>
        </v-flex>
    </v-layout>
</template>

<script>
export default {
    data() {
        return {
           pic: '',
           loadingPic: 'https://peepalfarm.github.io/images/loadinganimation.gif',
           fetchPicApi: 'https://dog.ceo/api/breed/' + this.$route.params.breed + '/images/random'
        }
    },
    methods: {
        getDog() {
            this.pic = this.loadingPic;
            this.$axios.get(this.fetchPicApi)
            .then((data) => {
                this.pic = data.data.message;
            });
        }
    },
    created() {        
        this.getDog();
    }
}
</script>
