<template>
    <v-layout column>
        <v-flex xs12>
            <v-card>
                <v-alert type="info" :value="true">
                    <h3>Welcome! Here's  a random pic of a cute doggy (depend on luck... sometime it's not always a cute one)</h3>
                </v-alert>
                <v-card-media
                    :src="randomPic"
                    height="250px"
                    contain
                >
                </v-card-media>
            </v-card>
            <v-card>
                <v-card-text>
                    <h3>This is a Vue JS testing project created via:</h3>
                    <v-list>
                        <v-list-tile><a href="https://vuejs.org/" target="_blank">- Vue JS</a></v-list-tile>
                        <v-list-tile><a href="https://vuetifyjs.com/en/" target="_blank">- Vuetify (UI framework)</a></v-list-tile>
                        <v-list-tile><a href="https://router.vuejs.org/" target="_blank">- vue-router (Routing)</a></v-list-tile>
                        <v-list-tile><a href="https://vuex.vuejs.org/" target="_blank">- Vuex (State Management)</a></v-list-tile>
                    </v-list>
                </v-card-text>
                <v-card-actions>
                    <v-flex d-flex xs12 sm4 offset-sm4>
                        <v-btn 
                            color="primary"
                            @click="visitDog"
                        >
                            Visit dog section
                        </v-btn>
                    </v-flex>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
var randomGogPicApi = "https://dog.ceo/api/breeds/image/random";
export default {
    data() {
        return {
            randomPic: "https://peepalfarm.github.io/images/loadinganimation.gif"
        }
    },
    methods: {
        visitDog: function() {this.$router.push('/dog')}
    },
    created() {
        this.$axios.get(randomGogPicApi)
        .then((data) => {
            this.randomPic = data.data.message;
        });
    }
}
</script>
