<template>
  <v-bottom-nav
    id="bottom-nav"
    absolute
    :active.sync="activeBtn"
    class="hidden-sm-and-up"
  >
    <v-btn v-for="(nav, index) in navs" :key="index" :to="nav.path" flat color="light-blue accent-4">
      <span>{{ nav.title }}</span>
      <v-icon>{{ nav.icon }}</v-icon>
    </v-btn>
  </v-bottom-nav>
</template>

<script>
  export default {
    watch: {
        '$route' (to) {
            this.activeNav(to.path);
        }
    },
    data () {
      return {
        activeBtn: 0
      }
    },
    computed: {
      navs: function() {return this.$store.state.navigation}
    },
    methods: {
      activeNav(path) {
        let regex = /\/([a-zA-Z]+).?/;
        let matched = regex.exec(path);
        let pathModule = (matched != null && matched[1]) ? matched[1] : 'home';
        switch(pathModule.toLowerCase()) {
          case "dog":
            this.activeBtn = 1;
            break;
          case "setting":
            this.activeBtn = 2;
            break;
          default:
            this.activeBtn = 0;
            break;
        }
      }
    },
    created() {
      this.activeNav(this.$route.path);
    }
  }
</script>

<style scaffold>
  #bottom-nav {
    position: fixed;
    bottom: 60px;
  }
</style>
