<template>
  <v-layout
    wrap
  >
    <v-navigation-drawer
      :value="drawer"
      absolute
      temporary
      @input="resetDrawer"
    >
      <v-list class="pa-1">
        <v-list-tile avatar>
          <v-list-tile-avatar>
            <img src="http://icons.iconarchive.com/icons/google/noto-emoji-animals-nature/256/22215-dog-icon.png">
          </v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title>My Dog House</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>

        <v-list-tile
          v-for="item in items"
          :key="item.title"
          @click="navigate(item.path)"
        >
            <v-list-tile-action >
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>

            <v-list-tile-content>
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
  </v-layout>
</template>

<script>
export default {
    computed: {
      drawer: function() {return this.$store.state.drawerExpand},
      items: function() {return this.$store.state.navigation}
    },
    methods: {
      navigate: function(path){
        this.$router.push(path);
      },
      // toggle drawser state to false when closed
      resetDrawer: function(expand) {
        if (!expand) this.$store.dispatch('toggleDrawer');
      }
    }
}
</script>
