import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
    strict: false,
    state: {
        nightMode: false,
        drawerExpand: false,
        navigation: [
            { title: 'Home', path: '/', icon: 'home'},
            { title: 'Dog', path: '/dog', icon: 'pets'},
            { title: 'Setting', path: '/setting', icon: 'settings'}
        ]
    },
    actions: {        
        toggleNightMode: (context, payload) => {
            if (localStorageAvailable()) localStorage.setItem('nightMode', payload);
            context.commit('setNightMode', payload)
        },
        toggleDrawer: (context) => {context.commit('setDrawerExpand', !context.state.drawerExpand)},
        initialiseStore: (context) => {
            if (localStorageAvailable()) {
                let existingNightMode = localStorage.getItem('nightMode');
                if (typeof(existingNightMode) != 'undefined') context.commit('setNightMode', existingNightMode === 'true');
            }
        }
    },
    mutations: {
        setNightMode: (state, payload) => state.nightMode = payload,
        setDrawerExpand: (state, payload) => state.drawerExpand = payload
    }
});

const localStorageAvailable = () => typeof(localStorage) !== 'undefined';