<template>
    <v-layout row wrap>
        <v-flex xs12>
            <v-card>
                <v-card-media :src="pic" alt="Select your favorite breed" height="350px" contain ></v-card-media>
            </v-card>
            <v-alert type="info" :value="true">
                <h3>Choose a breed from the following</h3>
            </v-alert>
            <v-list v-if="allBreed">
                <template v-for="(breed, index) in allBreed">
                    <v-list-tile :key="index">
                        <v-list-tile-content>
                            <router-link :to="'/dog/' + index" exact>{{ index }}</router-link>
                        </v-list-tile-content>
                    </v-list-tile>
                    <v-divider></v-divider>
                </template>
            </v-list>
        </v-flex>
    </v-layout>
</template>

<script>

export default {
    data() {
        return {
            pic: '',
            getPicApi: '',
            allBreed: '',
            loadingPic: 'https://peepalfarm.github.io/images/loadinganimation.gif',
            randomDogApi: 'https://dog.ceo/api/breeds/image/random',
            allDogApi: 'https://dog.ceo/api/breeds/list/all'
        }
    },
    methods: {
        getDog() {
            this.$axios.get(this.getPicApi)
            .then((data) => {
                this.pic = data.data.message;
            });
        },
        getAllDogList() {
            this.$axios.get(this.allDogApi)
            .then((data) => {
                this.allBreed = data.data.message;
            });
        }
    },
    created() {
        this.getPicApi = this.randomDogApi;
        this.pic = this.loadingPic;
        this.getDog();
        this.getAllDogList();
    }
}
</script>
