<template>
    <v-layout row>
        <v-flex xs12>
            <h3>Setting</h3>
            <v-card>
                <v-switch label="Night Mode" :input-value="nightMode" @click.native="toggleNightMode" ></v-switch>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
export default {
    computed: {
        nightMode() {return this.$store.state.nightMode}
    },
    methods: {
        toggleNightMode: function($event) {
            this.$store.dispatch('toggleNightMode', !this.nightMode);
        }
    }
}
</script>

<style scaffold>
    label {
        font-size: 26px;
    }
    .v-input__control {
        padding: 10px;
    }
</style>
